<template>
    <div>
        <div class="row justify-content-center">
          <div class="col-md-6">
            
          <p class="text-center font-bold text-sm p-3 uppercase">
      Profile :
    </p>
    <div class="text-center p-3">
      <img src="@/static/kopdiskominfo.png" class="kinoLightBox img-fluid " style="">
      
    </div>
        <div class="p-3">
            <form action="" @submit.prevent="ganti">
                <div class="sm-form ">
                    <label for="nip" class="capitalize">nip</label>
                    <input type="text" disabled id="nip" name="nip" class="form-control form-control-sm" placeholder="nip" v-model="vdata.nip" >
                </div>
                <div class="sm-form ">
                    <label for="nama" class="capitalize">nama</label>
                    <input type="text" id="nama" name="nama" class="form-control form-control-sm" placeholder="nama" v-model="vdata.nama" >
                </div>
                <div class="sm-form ">
                    <label for="jabatan" class="capitalize">jabatan</label>
                    <input type="text" id="jabatan" name="jabatan" class="form-control form-control-sm" placeholder="jabatan" v-model="vdata.jabatan" >
                </div>
                <div class="sm-form ">
                    <label for="unit_kerja" class="capitalize">unit_kerja</label>
                    <input type="text" id="unit_kerja" name="unit_kerja" class="form-control form-control-sm" placeholder="unit_kerja" v-model="vdata.unit_kerja" >
                </div>
                <br>
                <div class="text-center">
                    <button type="submit" class="btn btn-sm btn-primary  ">Simpan</button>
                </div>
            </form>
        </div>
          </div>
        </div>
    </div>
</template>
<script>

import autophp from "@/plugins/autophp.js";
let sdb = new autophp();
export default{
    data(){
        return{
            vdata:{}
        }
    },
    methods:{
        ganti(){
            //idnya letak di vdata
            this.vdata.id=this.$store.state.users.id;
            sdb.collection('tbuser').doc().set(this.vdata).then(res=>{
                console.log(res)
                 sdb.collection().ceklogin(this.$store).then(res=>{
                          }).catch(err=>{
                          })
            })
        }
    },
    mounted() {
        setTimeout(() => {
            this.vdata.nama=this.$store.state.users.nama
            this.vdata.nip=this.$store.state.users.nip
            this.vdata.jabatan=this.$store.state.users.jabatan
            this.vdata.unit_kerja=this.$store.state.users.unit_kerja
            this.$forceUpdate();
        }, 1500);
    },
    layout:"kominfo"
}
</script>